@import "https://fonts.googleapis.com/css2?family=Minecraftia&display=swap";
canvas {
  display: none;
}

body {
  color: #fff;
  background-image: url("https://i.postimg.cc/W1j6zxMm/wallpaperbetter-com-3840x2160-3.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Minecraftia, sans-serif;
  display: flex;
}

h1 {
  text-shadow: 3px 3px 6px #000;
  text-align: center;
  margin: 10px;
  font-size: 24px;
  font-weight: bold;
}

@media (width >= 1280px) and (height >= 720px) {
  canvas {
    display: block;
  }

  h1 {
    display: none;
  }
}

@media only screen and (width <= 600px) {
  h1 {
    font-size: 18px;
  }
}

